@import url(https://cdn.materialdesignicons.com/4.5.95/css/materialdesignicons.min.css);
body, html {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'sans-serif'
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.bg {
    background-size: cover;
}
.Register_text {
    margin-top: 30px;
    text-align: center;
}

.Googlebtn {
    margin-top: 10px;
}

@media all and (min-width: 480px) {
    .Login {
        padding: 60px 0;
    }

    .Login form {
        margin: 0 auto;
        max-width: 320px;
    }
}
.gallery {
    display: flex;
    padding: 0px 100px 0px 100px;
    flex-direction: column;
    margin-top: 20px;
}

.gallery span {
    font-weight: bold;
}

.gallery img {
    width: 50%;
    height: 50%;
}

ul,
li {
    list-style-type: none;
}
.desc {
}

.tags {
    font-size: 200;
}
.tag {
    margin: 40px;
    border: 5px dotted pink;
}

.desc_date {
    padding-bottom: 70px;
}

.dropdown-toggle,
.dropdown-menu {
    width: 120px;
}

#results-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}

.colsDir {
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
    align-self: center;
    text-align: center;
}

#results-container-centered {
    display: flex;
    flex-direction: column;
    padding: 10px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

#centered-image {
    margin-top: 20px;
    justify-content: center;
    display: flex;
    align-items: center;
}

#image-container {
  display: flex;
  justify-content: center;
  align-items: center;

  margin-bottom: 20px;
  padding: 20px;
}

#canvas-container {
  width: 100%;
  max-width: 600px;
  height: auto;
}

#image-container img {
  width: 100%;
  max-width: 600px;
  height: auto;
}

#webcam-container {
  display: flex;
  width: 100%;
  max-width: 300px;
  height: auto;
}
